import {mapState,mapActions} from 'vuex'
import $http from '@/js_sdk/zhouWei-request/requestConfig'
import baseUrl from '@/utils/url.js'

export default {
  name: 'home',
  data (){
  	return {
		submitData:{
			card_no: uni.getStorageSync('CARDID')||'',
			name: '',
			phone:'',
			identity_no:'',
			front_addr:'',
			verso_addr:'',
			hand_addr:''
		},
		dataErrTip:{
			card_no: '卡号不能为空',
			name: '卡号不能为空',
			phone:'手机号不能为空',
			identity_no:'身份证号不能为空',
			front_addr:'请上传身份证正面照',
			verso_addr:'请上传身份证反面照',
			hand_addr:'请上传手持身份证照'
		},
		indexIdCardBgFront:this.imgs.indexIdCardBgFront,
		indexIdCardBgBack:this.imgs.indexIdCardBgBack,
		indexIdCardBghHold:this.imgs.indexIdCardBghHold
  	}
  },
  computed: {
  	...mapState('homeData',[
		'navaInfo'
	]),
  },
  mounted: function(){
	
  },
  methods:{
	picFrontChange: function(e){
		let data = $http.qnImgUpload({
			count:1,//默认 9
			sizeType: ['compressed'],//默认 ['original', 'compressed']
			sourceType: ['album','camera'],//默认 ['album','camera']\
			data:{}
		},
		(res) => {
			//这里是每上传一张都返回一张图片地址
			console.log(res)
			if (res.length>8) {
				this.indexIdCardBgFront = 'http://file.iot886.com/'+res
				this.submitData.front_addr = 'http://file.iot886.com/'+res
				uni.hideLoading();
			}else {
				uni.showToast({
					title: '上传失败，请重新上传',
					icon: 'none'
				})
			}
		},
		err=>{
			console.log(err)
		});
	},
	picBackChange:function (){
		let data = $http.qnImgUpload({
			count:1,//默认 9
			sizeType: ['compressed'],//默认 ['original', 'compressed']
			sourceType: ['album','camera'],//默认 ['album','camera']\
			data:{}
		},
		(res) => {
			//这里是每上传一张都返回一张图片地址
			console.log(res)
			this.indexIdCardBgBack = 'http://file.iot886.com/'+res
			this.submitData.verso_addr = 'http://file.iot886.com/'+res
			uni.hideLoading();
		},
		err=>{
			console.log(err)
		});
	},
	picHoldChange:function(){
		let data = $http.qnImgUpload({
			count:1,//默认 9
			sizeType: ['compressed'],//默认 ['original', 'compressed']
			sourceType: ['album','camera'],//默认 ['album','camera']\
			data:{}
		},
		(res) => {
			//这里是每上传一张都返回一张图片地址
			console.log(res)
			this.indexIdCardBghHold = 'http://file.iot886.com/'+res
			this.submitData.hand_addr = 'http://file.iot886.com/'+res
			uni.hideLoading();
		},
		err=>{
			console.log(err)
		});
	},
	checkPhone:function (e) {
		this.submitData.phone = e.target.value
		if (!(/^1[34578]\d{9}$/).test(e.target.value)){
			this.$alert('手机号格式不正确', '提示', {
				confirmButtonText: '确定',
				closeOnClickModal: true
			})
		}
	},
	submitForm: function () {
		// this.$router.push('/user')
		let subFlag=0
		for (let item in this.submitData) {
			if (this.submitData[item]=='') {
				// this.$alert(this.dataErrTip[item], '提示', {
				// 	confirmButtonText: '确定'
				// })
				uni.showToast({
					title: this.dataErrTip[item],
					icon: 'none'
				})
				subFlag+=1
				return false;
			}
		}
		if (subFlag==0) {
			uni.request({
				url: baseUrl+'wx/identity_auth',
				method:'POST',
				data: this.submitData,
				success: res => {
					console.log(res)
					if (res.statusCode==200 || res.data.code==0){
						// this.$alert(res.data.msg, '提示', {
						// 	confirmButtonText: '确定',
						// 	closeOnClickModal: true,
						// 	callback: action => {
						// 		this.$router.push('/')
						// 	}
						// })
						uni.showModal({
							title: '提示',
							content: res.data.msg,
							confirmText: '确定',
							success: () => {
								uni.switchTab({
									url: 'pages/user'
								})
							}
						})
					}
				}
			})
		}		
	}
  },
  components: {
    
  }
}
